<template>
  <div class="search">
    <div class="search-box-wrapper">
      <search-box ref="searchBox" @query="search"></search-box>
    </div>
    <div v-show="!query" class="shortcut-wrapper" ref="shortcut">
      <scroll class="shortcut" :data="scrollData" ref="scroll">
        <div>
          <div class="hot-key">
            <h1 class="title">热门搜索</h1>
            <ul>
              <li class="item" v-for="item in hotKey" @click="addQuery(item.k)">
                <span>{{item.k}}</span>
              </li>
            </ul>
          </div>
          <div class="search-history" v-show="searchHistory.length">
            <h1 class="title">
              <span class="text">搜索历史</span>
              <span  class="clear" @click="showConfirm">
                <i class="icon-clear"></i>
              </span>
            </h1>
            <search-list  :searches="searchHistory" @selectItem="addQuery" @deleteItem="deleteSearchHistory"></search-list>
          </div>
        </div>
      </scroll>
    </div>
    <div class="search-result" v-show="query"  ref="search">
      <suggest :query="query" ref="suggest" @listScroll="blurInput" @select="saveSearch"></suggest>
    </div>
    <confirm ref="confirm" @confirm="confirm" text="确定清空搜索历史吗？"></confirm>
    <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
import SearchBox from 'base/search-box/search-box'
import Suggest from 'components/suggest/suggest'
import {getHotKey} from 'api/search'
import {playListMixin,searchMixin} from 'common/js/mixin'
import {ERR_OK} from 'api/config'
import {mapActions} from 'vuex'
import  SearchList from 'base/search-list/search-list'
import  Scroll from 'base/scroll/scroll'
import Confirm from 'base/confirm/confirm'

  export default {
       mixins:[playListMixin,searchMixin],
       data(){
        return {
            hotKey:[],
          showSinger:true
        }
    },
      computed:{
        scrollData(){
            return this.searchHistory.concat(this.hotKey)
        }
      },
       created(){
           this._getHotKey()
       },
       components:{
           SearchBox,
           Suggest,
         SearchList,
          Scroll,
         Confirm
       },
       methods:{
        handlePlayList(playList){
          const bottom  = playList.length >0 ? '60px' :''
          this.$refs.search.style.bottom = bottom
          this.$refs.suggest.refresh()
          this.$refs.shortcut.style.bottom = bottom
        },
        _getHotKey(){
                getHotKey().then((res) => {
                   if(res.code === ERR_OK)
                   {
                       this.hotKey = res.data.hotkey.slice(0,10)
                   }
                })
             },
         showConfirm(){
            this.$refs.confirm.show()
         },
         confirm(){
            this.clearSearchHistory()
         },
      ...mapActions(['clearSearchHistory'])
    },
    watch:{
           query(newQuery){
               if(!newQuery)
               {
                   setTimeout(() => {
                       this.$refs.scroll.refresh()
                   },20)
               }
           }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .search
    .search-box-wrapper
      margin: 20px
    .shortcut-wrapper
      position: fixed
      top: 178px
      bottom: 0
      width: 100%
      .shortcut
        height: 100%
        overflow: hidden
        .hot-key
          margin: 0 20px 20px 20px
          .title
            margin-bottom: 20px
            font-size: $font-size-medium
            color: $color-text-l
          .item
            display: inline-block
            padding: 5px 10px
            margin: 0 20px 10px 0
            border-radius: 6px
            background: $color-highlight-background
            font-size: $font-size-medium
            color: $color-text-d
        .search-history
          position: relative
          margin: 0 20px
          .title
            display: flex
            align-items: center
            height: 40px
            font-size: $font-size-medium
            color: $color-text-l
            .text
              flex: 1
            .clear
              extend-click()
              .icon-clear
                font-size: $font-size-medium
                color: $color-text-d
    .search-result
      position: fixed
      width: 100%
      top: 178px
      bottom: 0
</style>



